* {
  margin: 0;
  padding: 0;
  list-style: none;
  font-family: 'Microsoft JhengHei';
  text-decoration: none; }

ul {
  margin: 0; }

[v-cloak] {
  display: none; }

body {
  background: #333; }

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #fff;
  opacity: 1;
  /* Firefox */ }
  @media all and (max-width: 480px) {
    ::placeholder {
      color: #333; } }

.wrapper {
  width: 1200px;
  margin: 0 auto; }
  @media all and (max-width: 768px) {
    .wrapper {
      width: 100%; } }

header {
  height: 92px;
  background: #7828b4;
  color: #fff;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-content: center; }
  @media all and (max-width: 480px) {
    header {
      height: 70px; } }
  header .logo {
    font-weight: bold;
    font-size: 32px;
    width: 30%;
    display: flex;
    align-items: center;
    justify-content: center; }
    @media all and (max-width: 480px) {
      header .logo {
        width: 100%;
        height: 70px;
        text-align: center;
        font-size: 24px; } }
  header .search {
    display: flex;
    align-items: center;
    position: relative;
    text-align: center;
    box-sizing: border-box; }
    @media all and (max-width: 480px) {
      header .search {
        color: #333;
        position: absolute;
        top: 75px;
        height: 50px;
        padding: 0 10%;
        left: 0;
        right: 0;
        margin: auto;
        box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2); } }
    header .search input {
      outline: none;
      background: transparent;
      border: none;
      padding-left: 10px;
      color: #fff; }
    header .search .forLine {
      display: block;
      bottom: 30px;
      background: #eee;
      height: 1px;
      width: 300px;
      position: absolute;
      left: 0; }
      @media all and (max-width: 480px) {
        header .search .forLine {
          background: #333;
          bottom: 10px;
          right: 0;
          margin: auto; } }

#container {
  padding: 0 38px;
  background: #f2f2f2;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }
  @media all and (max-width: 768px) {
    #container {
      padding: 0; } }
  #container .Features {
    border-bottom: 1px solid #ccc;
    padding: 20px 40px;
    position: relative; }
    #container .Features i {
      position: absolute;
      top: 10px;
      right: 30px; }
    @media all and (max-width: 768px) {
      #container .Features .fa-minus {
        display: none; } }
    @media all and (max-width: 480px) {
      #container .Features .content {
        display: none; } }
    @media all and (max-width: 768px) {
      #container .Features.active .fa-plus {
        display: none; } }
    @media all and (max-width: 768px) {
      #container .Features.active .fa-minus {
        display: inline-block; } }
    @media all and (max-width: 480px) {
      #container .Features.active .content {
        display: block; } }
  #container .aside {
    width: 300px;
    box-sizing: border-box;
    background: #ebebeb; }
    @media all and (max-width: 480px) {
      #container .aside {
        width: 100%;
        margin-top: 50px; } }
    #container .aside i {
      display: none; }
      @media all and (max-width: 480px) {
        #container .aside i {
          display: inline-block;
          color: #888; } }
    #container .aside li {
      vertical-align: top; }
      #container .aside li label {
        vertical-align: top;
        padding: 5px 0 0 3px;
        display: inline-block; }
    #container .aside h3 {
      padding: 10px 0; }
      @media all and (max-width: 480px) {
        #container .aside h3 {
          text-align: center; } }
    #container .aside select {
      width: 200px;
      height: 30px;
      border-radius: 5px;
      border: none; }
    #container .aside input {
      height: 20px;
      padding: 5px;
      border-radius: 5px;
      border: none; }
    #container .aside input[type="text"] {
      width: 150px; }
    #container .aside .date span:nth-of-type(2) {
      padding-left: 23px; }
    #container .aside .date input {
      display: inline-block;
      margin: 5px 0; }
  #container main {
    padding: 20px 0px 20px 30px;
    width: 72%;
    box-sizing: border-box; }
    @media all and (max-width: 768px) {
      #container main {
        width: 50%;
        padding-left: 10px; } }
    @media all and (max-width: 480px) {
      #container main {
        width: 100%;
        padding: 0 10px; } }
    @media all and (max-width: 480px) {
      #container main .dataCount {
        display: none; } }
    #container main .dataCount .resultCount {
      color: #9013fe; }
    #container main .tag_wrapper {
      padding: 10px 0; }
      @media all and (max-width: 480px) {
        #container main .tag_wrapper {
          display: none; } }
      #container main .tag_wrapper .tag {
        display: inline-block;
        border: 1px solid #9013fe;
        padding: 5px 20px;
        color: #9013fe;
        box-sizing: border-box;
        text-align: center;
        border-radius: 35px;
        min-width: 100px;
        height: 35px;
        cursor: pointer; }
    #container main .item_wrapper {
      padding-top: 20px;
      width: 780px; }
      @media all and (max-width: 768px) {
        #container main .item_wrapper {
          width: 100%; } }
  #container .item {
    box-sizing: border-box;
    background: #fff;
    display: flex;
    margin: 10px 0;
    cursor: pointer; }
    @media all and (max-width: 768px) {
      #container .item {
        width: 100%;
        display: block; } }
    #container .item .img_box {
      width: 220px;
      height: 220px;
      overflow: hidden; }
      #container .item .img_box:hover img {
        transform: scale(1.2, 1.2); }
      @media all and (max-width: 768px) {
        #container .item .img_box {
          width: 100%;
          height: 196px; } }
      #container .item .img_box img {
        height: 100%;
        transition-duration: .5s; }
        @media all and (max-width: 768px) {
          #container .item .img_box img {
            width: 100%;
            height: auto; } }
    #container .item .text_box {
      padding: 20px 0 0 20px; }
      #container .item .text_box h3 {
        color: #9013fe; }
      #container .item .text_box p {
        width: 520px;
        padding: 10px 0;
        box-sizing: border-box;
        margin: 0 auto; }
        @media all and (max-width: 768px) {
          #container .item .text_box p {
            width: 100%; } }
        @media all and (max-width: 480px) {
          #container .item .text_box p {
            padding-right: 20px; } }
      #container .item .text_box .author {
        font-weight: bold; }
      #container .item .text_box .inside_tag {
        width: 130px;
        height: 24px;
        line-height: 24px;
        background: #d7d7d7;
        border-radius: 24px;
        color: #fff;
        box-sizing: border-box;
        margin-left: 20px;
        text-align: center;
        display: inline-block; }
      #container .item .text_box .info {
        padding: 10px 0; }
        #container .item .text_box .info li {
          display: inline-block; }
          @media all and (max-width: 768px) {
            #container .item .text_box .info li {
              display: block;
              padding: 10px 0; } }
        #container .item .text_box .info span {
          color: #999;
          display: inline-block;
          padding: 0 10px; }

.item_detail {
  box-sizing: border-box;
  background: #fff;
  margin: 10px 0; }
  @media all and (max-width: 768px) {
    .item_detail {
      width: 100%;
      display: block; } }
  .item_detail img {
    width: 100%;
    transition-duration: .5s; }
  .item_detail .text_box {
    padding: 20px 0 0 20px; }
    .item_detail .text_box h2 {
      text-align: center;
      color: #9013fe; }
    .item_detail .text_box h3 {
      color: #9013fe; }
    .item_detail .text_box p {
      padding: 10px 0;
      box-sizing: border-box; }
    .item_detail .text_box .author {
      font-weight: bold; }
    .item_detail .text_box .inside_tag {
      width: 130px;
      height: 24px;
      line-height: 24px;
      background: #d7d7d7;
      border-radius: 24px;
      color: #fff;
      box-sizing: border-box;
      margin-left: 20px;
      text-align: center;
      display: inline-block; }
    .item_detail .text_box .info {
      padding: 10px 0; }
      .item_detail .text_box .info li {
        display: inline-block; }
        @media all and (max-width: 768px) {
          .item_detail .text_box .info li {
            display: block;
            padding: 10px 0; } }
      .item_detail .text_box .info span {
        color: #999;
        display: inline-block;
        padding: 0 10px; }
  .item_detail .back {
    background: #9013fe;
    color: #fff;
    width: 100px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    border-radius: 10px; }

.pagination {
  position: relative; }
  .pagination li {
    display: inline-block;
    margin: 0 5px; }
    .pagination li:active a {
      background: #9013fe;
      color: #fff; }
    .pagination li a {
      padding: .5rem 1rem;
      display: inline-block;
      border: 1px solid #ddd;
      background: #fff;
      color: #9013fe; }
      .pagination li a:hover {
        background: #eee; }
